import GEO from './types/GEO'
import CUT_GEO from './types/CUT_GEO'
import ROUTG from './types/ROUTG'
import ENDPATH from './types/ENDPATH'
import START_POINT from './types/START_POINT'
import LINE_EP from './types/LINE_EP'
import ARC_IPEP from './types/ARC_IPEP'


export default class Geometry {
	constructor(params) {
		this.geo = new GEO(params);
		this.children = [];
		this.endPath = new ENDPATH();
	}

	setStartPoint(params) {
		this.startPoint = new START_POINT(params);
	}

	addLineTo(params) {
		this.children.push(new LINE_EP(params));
	}

	addArcTo(params) {
		this.children.push(new ARC_IPEP(params));
	}

	toMacro() {
		return `${this.geo.toMacro()}
${this.startPoint.toMacro()}
${this.children.map(child => child.toMacro()).join('\n')}
${this.endPath.toMacro()}`;
	}
}

